.justify-end {
  justify-content: flex-end; }

.justify-start {
  justify-content: flex-start; }

.justify-space-between {
  justify-content: space-between; }

.flex-center, .swiper__btn {
  justify-content: center;
  align-items: center; }

.list-style-none {
  list-style-type: none; }

.ml-auto {
  margin-left: auto; }

.mr-auto {
  margin-right: auto; }

.mx-auto {
  margin-right: auto;
  margin-left: auto; }

.overflow-hidden {
  overflow: hidden; }

.font-italic {
  font-style: italic; }

.text-uppercase, .btn-contact, .header__navLink {
  text-transform: uppercase; }

.text-capitalize {
  text-transform: capitalize; }

.text-center, .SubpageBanner .text h1,
.SubpageBanner .text p {
  text-align: center; }

.text-right {
  text-align: right; }

.fullsize {
  width: 100%;
  height: 100%; }

.object-cover {
  object-fit: cover;
  object-position: center;
  background-size: cover; }

.object-contain {
  object-fit: contain;
  object-position: center;
  background-size: contain; }

.position-center-center, .SubpageBanner .text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.after-background-blur-color {
  position: relative; }
  .after-background-blur-color:after {
    content: '';
    position: absolute;
    background: var(--bg-after);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0; }

.max-width--1160px {
  max-width: 1160px;
  width: 100%;
  margin: 0 auto; }

.blur-up {
  filter: blur(10px);
  transition: filter 1s; }
  .blur-up.lazyloaded {
    filter: blur(0); }

.fw-700 {
  font-weight: 700; }

.pt-80, .pd-80 {
  padding-top: 60px; }
  @media (max-width: 1199.98px) {
    .pt-80, .pd-80 {
      padding-top: 45px; } }
  @media (max-width: 767.98px) {
    .pt-80, .pd-80 {
      padding-top: 30px; } }

.pb-80, .cartAside__col, .pd-80 {
  padding-bottom: 60px; }
  @media (max-width: 1199.98px) {
    .pb-80, .cartAside__col, .pd-80 {
      padding-bottom: 45px; } }
  @media (max-width: 767.98px) {
    .pb-80, .cartAside__col, .pd-80 {
      padding-bottom: 30px; } }

.pb-50, .pd-50 {
  padding-bottom: 50px; }
  @media (max-width: 1199.98px) {
    .pb-50, .pd-50 {
      padding-bottom: 40px; } }
  @media (max-width: 767.98px) {
    .pb-50, .pd-50 {
      padding-bottom: 30px; } }

.pt-50, .pd-50 {
  padding-top: 50px; }
  @media (max-width: 1199.98px) {
    .pt-50, .pd-50 {
      padding-top: 40px; } }
  @media (max-width: 767.98px) {
    .pt-50, .pd-50 {
      padding-top: 30px; } }

.fs-18 {
  font-size: 1.8rem; }

.fs-16 {
  font-size: 1.6rem; }

.fs-14 {
  font-size: 1.4rem; }

.mt-25 {
  margin-top: 2.5rem; }

.mt-30 {
  margin-top: 3rem; }

.mt-100 {
  margin-top: 100px; }

.t-left {
  text-align: left; }

.row--custom {
  margin-left: -10px;
  margin-right: -10px;
  display: flex;
  flex-wrap: wrap; }
  .row--custom .col,
  .row--custom [class*='col-'] {
    padding-left: 10px;
    padding-right: 10px; }

.cl-main {
  color: #f07c00; }
